import domLens from 'domlens';
// import domLens from 'domlens/src/domLens.js';
import Store from './store.js';
import { ACTION } from './consts.js';

const store = new Store({
  // inital state
  toggles: {
    one: {
      checked: false,
      toggleList: ['two'],
    },
    two: {
      checked: true,
      toggleList: ['one', 'three'],
    },
    three: {
      checked: false,
      toggleList: ['two'],
    },
  },
});

// Creates lens that can update the dom.
// It provides a way to map elements to state.
const lens = {
  '.toggle input': {
    checked: function(elm) {
      const id = elm.id;
      const isChecked = store.getState(`toggles.${id}.checked`);
      return isChecked;
    },

    onClick: function(evt) {
      const elm = evt.target;
      const id = elm.id;

      // trigger action
      store.action({
        type: ACTION.TOGGLE,
        id,
      });
    },
  },
};

function updateDom() {
  // custom forEach for the lens.
  // const forEach = (element) => {
  //   debugger;
  // };
  // domLens(forEach, lens);

  domLens(lens, store.getState());
}
// update the dom on store change
store.onChange = updateDom;
// render the inital store
updateDom();


// debugging
window.store = store;



