import { ACTION } from '../consts.js';

/**
 * Toggles a toggle by id.
 * @param  {Object} state - {toggles: {checked, toggleList}}
 * @param  {Object} action
 * @return {Object}
 */
export default function toggleById(state, action) {
  if (action.type !== ACTION.TOGGLE) { return state; }
  const { id } = action;
  const toggle = state.toggles[id];

  // toggle by id
  state.toggles[id].checked = !toggle.checked;

  if (!toggle.toggleList) {
    return state;
  }

  // toggle each item in the toggleList
  toggle.toggleList.forEach((toggleId) => {
    state.toggles[toggleId].checked = !state.toggles[toggleId].checked;
  });

  return state;
};



